    background-color: #e2e2e1;
  }
  .alignLeft {
    text-align: left;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  input {
    height: 20px;
    font-size: 1.0rem;
  }
  td.topics {
    background: #93efff;
  }
  .vertical-align{
    vertical-align: bottom;
  }

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  .btn,
  a.btn,
  button.btn {
    font-size: 0.8rem;
    font-weight: 700;
    line-height: 0.1;
    position: relative;
    display: inline-block;
    padding: 0.9rem 1rem;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    /*letter-spacing: 0.1em;*/
    color: #212529;
    border-radius: 0.6rem;  /*  */
    white-space: nowrap;
  }
  .btn-blue,
  a.btn-blue {
    color: #fff;
    background-color: #3464ff;
  }
  .btn-blue:hover,
  a.btn-blue:hover {
    color: #3b3b3b;
    background: #dce2fd;
  }

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  .selectbox {
    display: inline-flex;
    align-items: center;
    position: relative;
  }
  .selectbox::after {
    position: absolute;
    right: 15px;
    width: 10px;
    height: 7px;
    background-color: #535353;
    clip-path: polygon(0 0, 100% 0, 50% 100%);
    content: '';
    pointer-events: none;
  }
  .selectbox select {
    appearance: none;
    min-width: 230px;
    height: 2.0em;
    padding: .5em calc(.8em + 30px) .4em .6em;
    border: none;
    border-bottom: 2px solid #cccccc;
    background-color: #fff;
    color: #333333;
    font-size: 1em;
    cursor: pointer;
  }
  .selectbox select:focus {
    outline: none;
  }

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  table.packListOption{
    width: 100%;
  }
  tr.outputTopic{
    background-color: rgb(224, 245, 255)
  }
  .hover tr:hover {
    background: #93efff;
  }
  td.top {
    vertical-align: top;
  }
  td.filterSelect {
    text-align: right;
  }
  .packList{
    overflow: auto;
    width: 100%;
    height: 90vh; /*  */
  }
  .packList td.id {
    min-width: 30px;
    width: 30px;
  }
  .packList td.packCheck {
    min-width: 20px; /*  */
    width: 20px;
  }
  .packList td.division {
    min-width: 100px; /*  */
  }
  .packList td.name {
    min-width: 100px; /*  */
  }
  .packList td.price {
    min-width: 50px;
  }
  .packList td.degree {
    min-width: 30px;
  }
  .packList td.Contents {
    min-width: 80px;
  }
  .packList table{
    margin: 0;
    border-spacing: 0;
  }
  .packList th{
    border-right: 1px solid #999;
    border-bottom: 1px solid #999;
    background: #2589d0;
    position: sticky; /*  */
    top: 0;
    left: 0;
    color: #ffffff;
    height: 100px;
  }
  .packList td{
    border-right: 1px solid #999;
    border-bottom: 1px solid #999;
    padding: 5px;
  }
  .packList td.Num{
    white-space: nowrap; /*  */
    margin-left: auto;
    text-align: right;
  }
  .packList td.Str {
    /*white-space: nowrap; /*  */
    text-align: left;
  }
  .packList td.Str-Wrap {
    text-align: left;
  }
  .asc::after {
    content: ""
  }
  .desc::after {
    content: ""
  }

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  @media screen and (max-width: 920px) {
    div.minFont{
      font-size: 0.5em; /* OK */
    }
    .packList td.division { /*  */
      min-width: 50px; 
    }
    .packList td.name { /*  */
      min-width: 50px; 
    }
  }

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  .tooltipHelp  {
    display: inline-block;
    position: relative;
  }
  .tooltipHelp svg {
    width: 20px;
    height: 20px;
    padding: 3px;
    border: 2px solid #c6cdd3;
    border-radius: 50%;
    box-sizing: border-box;
    cursor: pointer;
  }
  .tooltipHelp p {
    display: flex;
    justify-content: center;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    bottom: -135px;
    left: 120px;
    transform: translateX(-50%);
    padding: .5em 1em;
    border-radius: 3px;
    background-color: #00000099;
    color: #fff;
    font-size: .7em;
    white-space: nowrap;
    transition: opacity .3s;
    text-align: left;
  }
  .tooltipHelp:hover > p {
    visibility: visible;
    opacity: 1;
  }
  .tooltipHelp p::before {
    position: absolute;
    top: 0px;
    width: 0px;
    height: 0px;
    background-color: inherit;
    clip-path: polygon(10% 0, 0 100%, 100% 100%);
    content: '';
  }

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  .buttonTopUnder {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border: none;
    border-radius: 50%;
    background-color: #2589d0;
  }
  .buttonTopUnder:hover {
    border: 1px solid #2589d0;
    background-color: #fff;
  }
  .buttonTopUnder:hover path {
    fill: #2589d0;
  }
  .posTop{
    position: absolute;
    z-index: 100;
    top: 170px;
    right: 40px;
  }
  .posUnder{
    position: absolute;
    z-index: 100;
    bottom: 4.5%;
    right: 40px;
  }
  .rollingV{
    transform: scale(1,-1);
  }

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  .accordionDivRel{
    position: relative;
    width: 190px;
    min-width: 190px;
  }
  .accordionDivPos{
    position: absolute;
    z-index: 100;
    top: 0px;
  }
  .accordionDiv { /*  */
    max-width: 500px;
    margin-bottom: 7px;
    background-color: #ffffff;
    border-bottom: 2px solid #cccccc;
    border-radius: 0px; /*  */
  }
  .accordionDiv summary { /*  */
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: .1em .8em .2em .3em;
    color: #333333;
    cursor: pointer;
  }
  .accordionDiv summary::-webkit-details-marker {
    display: none;
  }
  .accordionDiv summary::after { /*  */
    transform: translateY(-25%) rotate(45deg);
    width: 7px;
    height: 7px;
    margin-left: 55px;
    border-bottom: 3px solid #b6bdc3;
    border-right: 3px solid #b6bdc3;
    content: '';
    transition: transform .3s;
  }
  .accordionDiv[open] summary::after {
    transform: rotate(225deg);
    margin-left: 50px;
  }
  .accordionDiv p {
    transform: translateY(-10px);
    opacity: 0;
    margin: 0;
    padding: .3em 2em 1.5em;
    color: #333333;
    transition: transform .5s, opacity .5s;
  }
  .accordionDiv[open] p {
    transform: none;
    opacity: 1;
  }
  .checkDiv {
    text-align: left;
  }
  .checkDiv {
    display: flex;
    flex-wrap: wrap;
    gap: .5em .2em;
    border: none;
  }
  .checkDiv label, input[type='checkbox'] {
    display: flex;
    align-items: center;
    gap: 0 .5em;
    position: relative;
    cursor: pointer;
  }
  .checkDiv label:hover {
    background-color: #d6d5d5;
  }
