    content: '';
  }
  .checkDiv label:has(:checked)::after {
    position: absolute;
    top: 5px;
    left: 7px;
    transform: rotate(45deg);
    width: 5px;
    height: 10px;
    border: solid #2589d0;
    border-width: 0 3px 3px 0;
    content: '';
  }*/

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  .accordionConRel{
    position: relative;
    width: 240px;
    min-width: 240px;
  }
  .accordionConPos{
    position: absolute;
    z-index: 100;
    top: 0px;
  }
  .accordionCon { /*  */
    max-width: 500px;
    margin-bottom: 7px;
    background-color: #ffffff;
    border-bottom: 2px solid #cccccc;
    border-radius: 0px; /*  */
  }
  .accordionCon summary { /*  */
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: .1em .8em .2em .3em;
    color: #333333;
    cursor: pointer;
  }
  .accordionCon summary::-webkit-details-marker {
    display: none;
  }
  .accordionCon summary::after { /*  */
    transform: translateY(-25%) rotate(45deg);
    width: 7px;
    height: 7px;
    margin-left: 103px;
    border-bottom: 3px solid #b6bdc3;
    border-right: 3px solid #b6bdc3;
    content: '';
    transition: transform .3s;
  }
  .accordionCon[open] summary::after {
    transform: rotate(225deg);
    margin-left: 100px;
  }
  .accordionCon p {
    transform: translateY(-10px);
    opacity: 0;
    margin: 0;
    padding: .3em 2em 1.5em;
    color: #333333;
    transition: transform .5s, opacity .5s;
  }
  .accordionCon[open] p {
    transform: none;
    opacity: 1;
  }
  .checkCon {
    text-align: left;
  }
  .checkCon {
    display: flex;
    flex-wrap: wrap;
    gap: .5em .2em;
    border: none;
  }
  .checkCon label, input[type='checkbox'] {
    display: flex;
    align-items: center;
    gap: 0 .5em;
    position: relative;
    cursor: pointer;
  }
  .checkCon label:hover {
    background-color: #d6d5d5;
  }
  /*.checkCon label::before {
    width: 18px;
    height: 18px;
    border-radius: 3px;
    border: 2px solid #d6dde3;
    content: '';
  }
  .checkCon label:has(:checked)::after {
    position: absolute;
    top: 5px;
