    transform: rotate(45deg);
    width: 5px;
    height: 10px;
    border: solid #2589d0;
    border-width: 0 3px 3px 0;
    content: '';
  }*/

  /* -----------------------------------------*/
  /*  */
  /* -----------------------------------------*/
  .filterClear{
    text-align: left;
  }

  /* -----------------------------------------*/
  /* FLASH SALE */
